<template>
	<view class="container">
		<view class="tabsTop">
			<u-tabs :list="list" :is-scroll="false" :current="current" @change="change" height="110" bg-color="#03958C"
				inactive-color="#fff" active-color="#fff" :active-item-style="activeStyle"></u-tabs>
		</view>
		<view class="timeline_wrap" v-if="current==0">
			<view class="timeLine">
				<u-time-line>
					<u-time-line-item v-for="(item,index) in timeList" :key="index">
						<template v-slot:node>
							<image v-if="index == 0" class="timeIcon" src="@/static/salePerson/timeLine1.png" mode="">
							</image>
							<image v-else class="timeIcon2" src="@/static/salePerson/timeLine2.png" mode=""></image>
						</template>
						<template v-slot:content>
							<view class="timeRight">
								<view class="followExcute">{{item.followExcuteTime}} <text>{{item.behaviorName}}</text>
								</view>
								<view class="desp">{{item.content}}</view>
								<view>跟进人 {{item.salesmanName}}</view>
							</view>
						</template>
					</u-time-line-item>
				</u-time-line>
			</view>
			<!-- <view class="footer" @click="goFollow">{{state==6?'我来联系':'新增跟进'}}</view> -->
			<view class="footer" @click="robCus" v-if="state==6">我来联系</view>
			<view class="footer_three" v-else-if="state==1">
				<text class="setIn" @click="setInvalid">置为无效客户</text>
				<view class="footerRight">
					<text @click="regTap">到访登记</text>
					<text class="green" @click="goFollow">新增跟进</text>
				</view>
			</view>
			<view class="footer_flex" v-else>
				<text @click="setInvalid" v-if="state!=3 && state!=4 && state!=5 && state!=7">置为无效客户</text>
				<text @click="goFollow" :class="state==3 || state==4 || state==5 || state==7?'buleFooter':''">新增跟进</text>
			</view>
		</view>

		<view class="register" v-if="current==1">
			<view class="info_box">
				<view class="info_item">
					<text class="label">真实姓名</text>
					<u-input v-model="form.custName" type="text" placeholder="请填写客户姓名" />
				</view>
				<view class="info_item">
					<text class="label">性别</text>
					<view class="flexBet">
						<ld-select :underlist="sexList" list-key="label" value-key="value" placeholder="请选择性别"
							v-model="form.custGender" @change="sexChange"></ld-select>
						<uni-icons type="arrowright" size="12" color="#999"></uni-icons>
					</view>
				</view>
				<view class="info_item">
					<text class="label">手机号码</text>
					<u-input v-model="form.custPhone" type="text" maxLength="11" placeholder="请填写手机号码" />
				</view>
				<view class="info_item">
					<text class="label">分组</text>
					<view class="flexBet">
						<ld-select :underlist="form.groups" list-key="label" value-key="value" placeholder="请选择分组"
							v-model="form.groupIds" :multiple="true" @change="selectGroup"></ld-select>
						<uni-icons type="arrowright" size="12" color="#999"></uni-icons>
					</view>
				</view>
				<view class="info_item">
					<text class="label">婚姻状况</text>
					<view class="flexBet">
						<ld-select :underlist="marryList" list-key="label" value-key="value" placeholder="请选择婚姻状况"
							v-model="form.marryState" @change="marryChange"></ld-select>
						<uni-icons type="arrowright" size="12" color="#999"></uni-icons>
					</view>
				</view>

				<view class="info_item itemMargin">
					<text class="label">分组</text>
					<view class="flexBet">
						<ld-select :underlist="form.groups" list-key="label" value-key="value" placeholder="请选择分组"
							v-model="form.groupIds" :multiple="true" @change="selectGroup"></ld-select>
						<uni-icons type="arrowright" size="12" color="#999"></uni-icons>
					</view>
				</view>
				<view class="info_item">
					<text class="label">客户状态</text>
					<text>{{form.stateName}}</text>
				</view>
				<view class="info_item">
					<text class="label">意向级别</text>
					<view class="flexBet">
						<ld-select :underlist="form.intentionLevels" list-key="label" value-key="value"
							placeholder="请选择意向级别" v-model="form.intensionLevel" @change="selectIntenLeve"></ld-select>
						<uni-icons type="arrowright" size="12" color="#999"></uni-icons>
					</view>
				</view>
				<view class="info_item">
					<text class="label">认知途径</text>
					<view class="flexBet">
						<ld-select :underlist="form.cognitiveApproachs" list-key="label" value-key="value"
							placeholder="请选择认知途径" v-model="form.cognitiveApproach" @change="selectCognitive">
						</ld-select>
						<uni-icons type="arrowright" size="12" color="#999"></uni-icons>
					</view>
				</view>
				<remarkBox :textName="'备注'" :remarkValue="form.remark" isCusDet="1" @getValue="_getValue"></remarkBox>
			</view>
			<view class="footer" @click="saveTap" v-if="state!=6">保存</view>
		</view>
	</view>
</template>

<script>
	import ldSelect from '@/components/ld-select/ld-select.vue';
	import remarkBox from '@/components/remark.vue';
	import httpUtils from '@/utils/httpUtils.js';
	import Tools from '@/utils/tools.js';
	export default {
		data() {
			return {
				activeStyle: {
					fontSize: '36rpx'
				},
				list: [{
					name: '跟进记录'
				}, {
					name: '资料信息'
				}],
				state: '', // 客户类型
				current: 0,
				timeList: [], // 跟进list
				form: {}, // 客户资料
				// sexList: ['男', '女', '保密'], // 性别list
				// marryList: ['未婚', '已婚', '保密'], // 婚姻状态list
				sexList: [{
					label: '男',
					value: '0'
				}, {
					label: '女',
					value: '1'
				}, {
					label: '保密',
					value: '2'
				}],
				marryList: [{
					label: '未婚',
					value: '0'
				}, {
					label: '已婚',
					value: '1'
				}, {
					label: '保密',
					value: '2'
				}],
				groupList: [], // 分组列表
				cusId: '', // 客户id
			}
		},
		onLoad(options) {
			this.state = options.state
			this.cusId = options.id
			this._getDet(options.id)
		},
		methods: {
			// 获取详情
			_getDet(id) {
				let that = this
				Tools.showLoading('加载中')
				httpUtils.sendHttpRequest('2022', Object.assign({}, {
					id: id
				}), function(res) {
					Tools.hideLoading()
					uni.hideNavigationBarLoading()
					if (res.data.code == '0') {
						that.timeList = res.data.data.followLog
						that.form = res.data.data.info
					}
				})
			},
			// 性别选择
			sexChange(val) {
				this.form.custGender = val
			},
			// 婚姻状况选择
			marryChange(val) {
				this.form.marryState = val
			},
			// 意向级别选择
			selectIntenLeve(val) {
				this.form.intensionLevel = val
			},
			// 认知途径选择
			selectCognitive(val) {
				this.form.cognitiveApproach = val
			},
			// 分组选择
			selectGroup(val) {
				this.form.groupIds = val
			},
			// 备注回调
			_getValue(data) {
				this.form.remark = data
			},
			// tab切换
			change(index) {
				this.current = index;
			},
			// 我来联系
			robCus() {
				Tools.showLoading('联系中')
				httpUtils.sendHttpRequest('2034', Object.assign({}, {
					id: this.cusId
				}), function(res) {
					Tools.hideLoading()
					uni.hideNavigationBarLoading()
					if (res.data.code == '0') {
						Tools.showSuccessToast('操作成功')
						setTimeout(function() {
							uni.reLaunch({
								url: '/pages/salemanVisual/customer/index'
							})
						}, 500)
					}
				})
			},
			// 新增跟进
			goFollow() {
				uni.navigateTo({
					url: '/pages/salemanVisual/customer/followUp?id=' + this.cusId
				})
			},
			// 到访登记
			regTap() {
				let that = this
				uni.showModal({
					title: '提示',
					content: '确定用户已到场,并对其进行登记？',
					success: function(res) {
						if (res.confirm) {
							Tools.showLoading('登记中')
							httpUtils.sendHttpRequest('2047', Object.assign({}, {
								salesOrderId: that.timeList[0].salesOrderId
							}), function(res) {
								Tools.hideLoading()
								if (res.data.code == '0') {
									that.state = 2
									Tools.showSuccessToast('客户登记成功！')
									setTimeout(function() {
										const pages = getCurrentPages(); //当前页面栈
										if (pages.length > 1) {
											let beforePage = pages[pages.length -
											2]; //获取上一个页面实例对象  
											beforePage.$vm._getList(); //触发父页面中的方法_getList()
										}
									}, 500)
								}
							})
						}
					}
				});
			},
			// 置为无效客户
			setInvalid() {
				let that = this
				uni.showModal({
					title: '提示',
					content: '确定将该用户置为无效用户吗？',
					success: function(res) {
						if (res.confirm) {
							Tools.showLoading('设置中')
							httpUtils.sendHttpRequest('2046', Object.assign({}, {
								id: that.cusId
							}), function(res) {
								Tools.hideLoading()
								if (res.data.code == '0') {
									Tools.showSuccessToast('置为无效客户成功')
									setTimeout(function() {
										const pages = getCurrentPages(); //当前页面栈
										if (pages.length > 1) {
											let beforePage = pages[pages.length -
											2]; //获取上一个页面实例对象  
											beforePage.$vm._getList(); //触发父页面中的方法_getList()
										}
										uni.navigateBack({
											delta: 1
										})
									}, 500)
								}
							})
						}
					}
				});
			},
			// 保存客户信息
			saveTap() {
				Tools.showLoading('修改中')
				httpUtils.sendHttpRequest('2023', Object.assign({}, this.form), function(res) {
					Tools.hideLoading()
					uni.hideNavigationBarLoading()
					if (res.data.code == '0') {
						Tools.showSuccessToast('修改成功')
						setTimeout(function() {
							uni.reLaunch({
								url: '/pages/salemanVisual/customer/index'
							})
						}, 500)
					}
				})
			}
		},
		components: {
			ldSelect,
			remarkBox
		}
	}
</script>

<style lang="scss">
	@import '@/styles/form.scss';

	.container {
		.tabsTop {
			width: 100%;
			position: fixed;
			top: 0;
			z-index: 10;

			.u-tab-bar {
				background-color: #F5B00C !important;
			}
		}

		// h5持有样式
		/* #ifdef H5 */
		.tabsTop {
			top: 44px;
			border-bottom: 1px solid #eee;
		}

		/* #endif */
		.u-scroll-view {
			border-bottom: 1rpx solid #eee;
		}

		.timeline_wrap {
			padding-top: 120rpx;

			.timeLine {
				padding: 30rpx;
				padding-left: 60rpx;
				.timeIcon {
					width: 68rpx;
					height: 68rpx;
				}

				.timeIcon2 {
					width: 44rpx;
					height: 43rpx;
				}

				.timeRight {
					margin-left: 30rpx;

					view {
						font-size: 28rpx;
						color: #999;
						line-height: 46rpx;
					}

					.followExcute {
						font-size: 30rpx;
						color: #333;

						text {
							margin-left: 24rpx;
							font-size: 24rpx;
							color: #03958C;
							padding: 0rpx 12rpx;
							background: #F2F7F7;
							margin-right: 10rpx;
							border-radius: 6rpx;
							display: inline-block;
						}
					}

					.desp {
						color: #333;
					}
				}
			}
		}

		.register {
			padding-top: 120rpx;
			.info_box {
				.info_item {
					margin: 0 0 !important;
					padding: 0 30rpx;
				}

				.itemMargin {
					border-top: 20rpx solid #f6f6f6;
				}
			}
		}
		// 按钮
		.footer_flex{
			.buleFooter{
				color: #fff!important;
				background: linear-gradient(90deg, #03958C, #00AE66);
				margin: 0 95rpx;
			}
		}
	}
</style>
